                'text': 'CF',
                'value': '302'
              },
              {
                'text': 'Game mobile khác',
                'value': '399'
              }
            ]
          },
          {
            'index': 3,
            'color': '#75DA08',
            'platform': null,
            'category': '3',
            'subCategory': '399',
            'list': [
              {
                'text': 'Tất cả',
                'value': null
              },
              {
                'text': 'Liên quân',
                'value': '301'
              },
              {
                'text': 'CF',
                'value': '302'
              },
              {
                'text': 'Game mobile khác',
                'value': '399'
              }
            ]
          }
        ]
        this.index = 3
      },
      addRow: function () {
        this.index++
        var letters = '0123456789ABCDEF'
        var color = '#'
        for (var i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)]
        }
        try {
          this.data.splice(this.index, 0, {index: this.index, color: color, platform: null, category: null, subCategory: null})
        } catch (e) {
          console.log(e)
        }
      },
      removeRow: function (index) {
        this.data.splice(index, 1)
      },
      getData: function () {
        var self = this
        self.dataChart.labels = []
        self.dataChart.datasets = []
        for (var i = 0; i < self.data.length; i++) {
          $.ajax({
            context: 'application/json',
            type: 'GET',
            async: false,
            data: {
              report_type: this.reportType,
              platform: this.data[i].platform,
              category: this.data[i].category,
              sub_category: this.data[i].subCategory,
              dt_from: this.dt_from,
              dt_to: this.dt_to
            },
            url: 'http://monitor.talktv.vn/report/' + this.report + '/'
          }).done(function (msg) {
            self.dataChart.labels = msg.headers
            var objectChart = {}
            objectChart.label = self.nameObject(self.data[i].platform, self.data[i].category, self.data[i].subCategory)
            objectChart.backgroundColor = self.data[i].color
            objectChart.borderColor = self.data[i].color
            objectChart.lineTension = 0
            objectChart.data = msg.result
            objectChart.fill = false
            self.dataChart.datasets.push(objectChart)
            if (self.$refs.chart) {
              self.$refs.chart.chart.update()
            }
            self.chartjs = true
          }).fail(function (jqXHR, textStatus) {
            console.log('Request failed: ' + textStatus)
          })
        }
      },
      onChange: function (index) {
        this.data[index].list = JSON.parse(JSON.stringify(this.options(index)))
        this.data[index].subCategory = this.data[index].list[0].value
      },
      onChangePlatform: function (index) {
        if (this.data[index].platform === 'null') {
          this.data[index].platform = null
        }
      }
    }
  }
</script>


<style>
  .is-success-custom{
    background-color: rgba(0, 209, 178, 0.7);
    border-color: transparent;
    color: #fff;
  }
  .button:hover, .button.is-hovered {
    background-color: whitesmoke;
    color: #00d1b2;
    border-color: rgba(181, 181, 181, 0);
  }
</style>



